nav {
  background-color: #b2b2b2;
  border-bottom: 3px solid #ff4040;
  display: flex;
  justify-content: space-around;
  margin-bottom: 50px;
  margin-top: -20px;
}

nav p {
  margin-top: 0px;
}

nav p a {
  font-family: monospace;
  padding: 5px;
  border-radius: 5px;
}

nav p a:hover {
  background-color: #3c67ff;
}

@media only screen and (min-width: 600px) {
  .small-screen {
    display: none;
  }
}